.home-booking {
  width: 1230px;
  height: 224px;
  background: #ffffff;
  box-shadow: 0px 4px 30px rgba(77, 70, 250, 0.1);
  border-radius: 12px;
  margin: 127px 0px 0px 141px;
  padding: 30px;
  position: relative;
}
.home-booking:before {
  content: "";
  position: absolute;
  top: 210px;
  left: 88px;
  right: -141px;
  height: 118px;
  background-color: #f4f2f9;

  z-index: -1;
}
.home-booking-content {
  font-family: SVN-Biennale;
  font-size: 14px;
  font-weight: 600;
  line-height: 21px;
  letter-spacing: 0em;
  text-align: left;
}

.home-booking-selection {
  list-style: none;
  display: flex;
  align-items: center;
  gap: 0 20px;
}
.home-booking-selection li {
  display: flex;
  text-align: center;
}
