label {
  position: relative;
  cursor: pointer;
}

input[type="checkbox"] {
  display: none;
}

input[type="checkbox"] + .home-booking-content:before {
  width: 22px;
  height: 22px;
  border: 1.5px solid #f0f0f0;
  content: "";
  display: inline-block;
  margin: -2px 8px 0 0;
  padding: 0;
  vertical-align: top;
  border-radius: 50%;
}

input[type="checkbox"]:checked + .home-booking-content:before {
  background: #1460c8;
  border: 1.5px solid #1460c8;

  color: #333;
  content: "";
  text-align: center;
  border-radius: 50%;
}

input[type="checkbox"]:focus + .home-booking-content::before {
  outline: 0;
}

.home-selectMembers {
  position: relative;
  width: 134px;
  height: 21px;
  font-family: "SVN-Biennale";
  font-style: normal;
  font-weight: 600;
  font-size: 14px;
  line-height: 21px;
  margin-left: 30px;
}
.home-selectMembers-list {
  position: absolute;
  top: 24px;
  z-index: 20;
  background-color: #fff;
  left: 0;
}
.home-selectMembers-list li {
  display: flex;
  flex-direction: row;
  margin-top: 10px;
  justify-content: space-between;
}

.down-icon {
  color: #4d46fa;
  transform: scale(0.7);
  position: absolute;
  top: 5px;
  right: 0;
}
.home-business {
  position: relative;
  width: 134px;
  height: 21px;
  font-family: "SVN-Biennale";
  font-style: normal;
  font-weight: 600;
  font-size: 14px;
  line-height: 21px;
  margin-left: 30px;
  border: none;
}
.down-iconBusiness {
  color: #4d46fa;
  transform: scale(0.7);
  position: absolute;
  top: 5px;
  left: 287px;
  z-index: 2;
}

.home-business option {
  ont-family: "SVN-Biennale";
  font-style: normal;
  font-weight: 600;
  font-size: 14px;
  line-height: 21px;
  margin-left: 30px;
}
.home-bookingContainer {
  margin-top: 20px;
  gap: 0 20px;
  display: flex;
}
.home-bookingContainer-exchange {
  display: flex;
  gap: 0 20px;
  position: relative;
}

.home-bookingContainer-datePicker {
  width: 530px;
  height: 100px;
  background: #ffffff;
  border: 1px solid rgba(0, 0, 0, 0.1);
  border-radius: 12px;
  display: flex;
  padding: 15px 20px 12px 20px;
  justify-content: flex-start;
}
.departure {
  width: 70%;
}
.departure-title,
.return-title {
  font-family: "SVN-Biennale";
  font-style: normal;
  font-weight: 600;
  font-size: 12px;
  line-height: 18px;
  letter-spacing: 0.1em;
  text-transform: uppercase;

  color: #000000;

  opacity: 0.5;
}
.date {
  font-family: "SVN-Biennale";
  font-style: normal;
  font-weight: 600;
  font-size: 24px;
  line-height: 36px;

  color: #4d46fa;
}

.prevBtn,
.nextBtn {
  border: none;
  background-color: transparent;

  width: 29px;
  height: 21px;

  font-family: "SVN-Biennale";
  font-style: normal;
  font-weight: 600;
  font-size: 14px;
  line-height: 21px;
}
.prevBtn {
  margin-right: 19px;
}
.fromForm {
  padding: 15px 20px 12px 15px;
  width: 300px;
  height: 100px;
  background: #ffffff;
  border: 1px solid rgba(0, 0, 0, 0.1);
  border-radius: 12px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.toForm {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 15px 20px 12px 25px;
  width: 300px;
  height: 100px;
  background: #ffffff;
  border: 1px solid rgba(0, 0, 0, 0.1);
  border-radius: 12px;
}
.exchangeBtn {
  position: absolute;
  width: 60px;
  height: 60px;

  border-radius: 50%;
  left: 50%;
  top: 50%;
  background-color: #fff;
  transform: translate(-50%, -50%);
  border: 1px solid rgba(0, 0, 0, 0.1);
}
.trimTop {
  position: absolute;
  height: 20px;
  background-color: #fff;
  width: 20px;
  z-index: 99;
  left: 50%;
  top: 15%;
  transform: translate(-50%, -50%);
}
.trimBot {
  position: absolute;
  height: 20px;
  background-color: #fff;
  width: 20px;
  z-index: 99;
  left: 50%;
  top: 85%;
  transform: translate(-50%, -50%);
}
.exchangeBtn-icon {
  transform: scale(1.2);
}

select {
  /* for Firefox */
  -moz-appearance: none;
  /* for Chrome */
  -webkit-appearance: none;
}

/* For IE10 */
select::-ms-expand {
  display: none;
}

.searchFlight-btn {
  margin: 20px 0 0 925px;
  right: 20px;
  width: 245px;
  height: 60px;
  background: #4d46fa;
  border-radius: 12px;
  border: none;
  font-family: "SVN-Biennale";
  font-style: normal;
  font-weight: 600;
  font-size: 18px;
  line-height: 27px;

  padding: 17px 20px 16px 20px;
}
.searchFlight-btn a {
  text-decoration: none;
  color: #ffffff;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.searchFlight-btn a .next-Icon path {
  stroke: #ffff;
}
.rw-widget-input,
.rw-widget {
  color: #4d46fa;
}
