.home {
  width: 100%;
  position: relative;
}

.home-hero {
  padding: 156px 0px 0px 171px;
}
.home-hero p {
  font-family: "SVN-Biennale";
  font-style: normal;
  font-weight: 400;
  font-size: 72px;
  line-height: 105px;
}
.home-img {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  z-index: -1;
}
.home-img img {
  width: 100%;
  object-fit: cover;
  height: 803px;
  object-position: 20% 38%;
}
.home-outlay {
  position: fixed;
  width: 100%;
  top: 0;
  left: 0;
  right: 0;
  background-color: black;
  z-index: 10;
}
